import { configureStore } from '@reduxjs/toolkit'
import game from '../features/game/gameSlice'
import initialState from './initialState'


let persistedState = (
  localStorage && localStorage.getItem('ZombieChamber-GameSave') ? JSON.parse(localStorage.getItem('ZombieChamber-GameSave'))
  : initialState
)

const store = configureStore({
  reducer: {
    game
  },
  preloadedState: {
    persistedState
  }
})

store.subscribe(() => {
  if (localStorage) {
    localStorage.setItem('ZombieChamber-GameSave', JSON.stringify(store.getState().game))
  }
})


export default store
